// MAIN

#page-row {
  margin-bottom: 20px;

  .image {
    display: flex;
    justify-content: center;
    margin: 20px 0;

    a {
      overflow: hidden;

      img {
        height: 300px;
        padding: 0;
        min-width: 300px;
        transition: transform 1s, opacity 1s;

        &:hover {
          transform: scale(1.7);
        }
      }
      &:hover {
        z-index: 1;


      }
    }
  }
}

.page-about {
  text-align: center;

  p {
    margin: 20px 0;
    font-size: 2rem;
  }
}

.insta {
  color: black;
  font-size: 2rem;

  a {
    color: #f14578;

    &:hover {
      color: #f14578;
      cursor: pointer;
    }
  }
}
// LIGHTBOX

#lightbox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8) repeat;
  text-align: center;
  z-index: 3;

  p {
    text-align: right;
    color: #fff;
    margin-right: 70px;
    margin-top: 70px;
    font-size: 2rem;
    cursor: pointer;
  }

  #content {
    display: flex;
    justify-content: center;
    width: 100%;

    img {
      box-shadow: 0 0 25px #111;
      -webkit-box-shadow: 0 0 25px #111;
      -moz-box-shadow: 0 0 25px #111;
      position: absolute;
      top: 10%;
      left: 25%;
      height: auto;
      width: 50%;
      margin: 50px auto auto;
    }
  }


}
